<template>
	<div>
		<div class="item">
							<div>
								<div class="item-img"><img :alt="item.name_title" :src="item.sku_info[itemIndex].ali_image+'?x-oss-process=image/resize,w_206/quality,Q_80/format,webp'" style="opacity: 1;">
								</div>
								<h6>{{item.name}}</h6>
								<h3 >{{item.name_title}}</h3>
								<div class="params-colors">
									<ul class="colors-list">
										<li v-for="item,index in item.sku_info">
										<a href="javascript:;"><img :src="'http://img01.smartisanos.cn/'+item.spec_json.image+'20X20.jpg'" @click="tab(index)">
										</a>
										</li>
									</ul>
								</div>
								<div class="item-btns clearfix">
									<span class="item-gray-btn"><router-link :to="{name:'Item',query:{itemId:item.sku_info[itemIndex].sku_id}}">查看详情</router-link></span><span class="item-blue-btn" @click="addCar(item.sku_info[itemIndex])">加入购物车 </span>
								</div>
								<div class="item-price clearfix">
									<i>¥</i><span>199.00</span>
								</div>
								<div class="discount-icon">false</div>
								<div class="item-cover">
										<router-link :to="{name:'Item',query:{itemId:item.sku_info[itemIndex].sku_id}}"></router-link>
								</div>
							</div>
						</div>
	</div>
</template>
<script type="text/javascript">
	export default {
		name:'shop-item',
		data (){
			return {
				itemIndex: 0
			}
		},
		methods:{
			tab(index){
				console.log(index)
				// 点击那个弹出哪个的下标
				this.itemIndex = index;
			},
			addCar(data){
				this.$store.commit('addCarpanelData',data)
			}
			// addCarpanelData(data){
			// 	this.$store.commit('addCarpanelData',data)
			// }
		},
		props:["item"]
	}
</script>
<style type="text/css">
	
</style>


