import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex)

let store = new Vuex.Store({
	// 存放数据
	state:{
		carPanelData:[],
		carShow: false
	},
	getters:{
		// 数量
		totleCount(state){
			let count = 0;
			state.carPanelData.forEach(goods =>{
				count += goods.count
			})
			return count;
		}
		,
		// 价格
		totlePrice(state){
			let price = 0;
			state.carPanelData.forEach(goods =>{
				price += goods.count * goods.price
			})
			return price;
		},
		// 全选的功能
		 allChecked (state) {
      let allChecked = true
      state.carPanelData.forEach((goods) => {
        if(!goods.check){
          allChecked = false
          return
        }
      })
      return allChecked
    }
	},
	mutations:{
		addCarpanelData(state,data){
			let boff = true;
			state.carPanelData.forEach(goods=>{
				if(goods.sku_id === data.sku_id){
					goods.count ++;
					boff = false;
					state.carShow= true;

				}

			});
			if(boff){
				state.carShow= true;
				let goodsData = data;
				// 因为原始数据里面没有count属性，这个是为了区分加入了同一件商品加入了几次，
				// 原始数据就会添加count属性了，
				Vue.set(goodsData,'count',1)
				state.carPanelData.push(goodsData);
			}
			console.log(state.carPanelData)
		},
		delCarpanelData(state,id){
			// 删除购物车的数据
			state.carPanelData.forEach((goods,index)=>{
				if(goods.sku_id === id){
					state.carPanelData.splice(index,1)
					return;
				}
			})
		},
		// 显示购物车
		showCar(state){
			state.carShow = true;
		},
		// 隐藏购物车
		hideCar(state){
			setTimeout(()=>{
			state.carShow = false;
			},800)
		},
		// 添加收获信息
		submitInfo(state,data){
			if(data.default){
				state.receiveInfo.forEach((receive) =>{
					receive.default = false
				})
				state.receiveInfo.push(data)
			}
		},
		plusCarPanelData(state,id){
			state.carPanelData.forEach((goods,index)=>{
				if(goods.sku_id == id){
					if(goods.count >= goods.limit_num) return;
					goods.count ++;
					return;
				}
			})
		},
		subCarPanelData(state,id){
			state.carPanelData.forEach((goods,index)=>{
				if(goods.sku_id === id){
					if(goods.count <= 1) return;
					goods.count --;
					return;
				}
			})
		}
	}
})


export default store


